import axios from 'axios';

const API_URL = import.meta.env.VITE_API_URL || 'http://localhost:8000';
const WS_URL = API_URL.replace('http', 'ws');

const API = axios.create({ baseURL: API_URL });
API.interceptors.request.use(config => {
  const token = localStorage.getItem('token');
  if (token) config.headers.Authorization = `Bearer ${token}`;
  return config;
});

// Error handling interceptor
API.interceptors.response.use(
  response => response,
  error => {
    if (error.response?.status === 401) {
      localStorage.removeItem('token');
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

// Auth API
export async function login(data) {
  const formData = new FormData();
  formData.append('username', data.username);
  formData.append('password', data.password);
  
  const res = await API.post('/auth/login', formData, {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
    },
  });
  localStorage.setItem('token', res.data.access_token);
  return res;
}

export async function logout() {
  localStorage.removeItem('token');
  window.location.href = '/login';
}

// Test API
export async function startTest(config) {
  const res = await API.post('/tests/run', config);
  return res.data;
}

export async function getTestStatus(taskId) {
  const res = await API.get(`/tests/status/${taskId}`);
  return res.data;
}

// Analytics API
export async function fetchYieldData() {
  const res = await API.get('/analytics/yield');
  return res.data;
}

export async function fetchWaferMap(id) {
  const res = await API.get(`/analytics/wafer_map/${id}`);
  return res.data;
}

// WebSocket API
export function connectToTestStream(waferId, onMessage) {
  const ws = new WebSocket(`${WS_URL}/tests/ws/${waferId}`);
  
  ws.onopen = () => {
    console.log(`Connected to wafer ${waferId} test stream`);
  };
  
  ws.onmessage = (event) => {
    const data = JSON.parse(event.data);
    onMessage(data);
  };
  
  ws.onerror = (error) => {
    console.error('WebSocket error:', error);
  };
  
  ws.onclose = () => {
    console.log(`Disconnected from wafer ${waferId} test stream`);
  };
  
  return {
    disconnect: () => ws.close(),
    isConnected: () => ws.readyState === WebSocket.OPEN
  };
}
